*{
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.6rm;
}

body {
  padding : 60px;
}

input{
  color: grey;
  font-size: 1.2rem;
  width:100%;
}

.card-grid{

  display:flex;
  flex-direction: row;
  flex-wrap: wrap;
}



.card{
  align-content: center;
  border:1px solid grey;
  border-radius: 6px;
  margin-bottom:12px;
  margin-right: 12px;
  overflow: hidden;

}

.card p {
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 170px;
  margin:4px;
  border-radius: 6px;

}

